@import "vendors/index.css";

/* Custom here */

:root {
  --accent-color: rgb(235, 84, 0);
  --red-color: rgb(192, 0, 9);
}

html {
  font-size: 18px;
}

body {
  font-family: "Comfortaa", serif;
  line-height: 1.3;
}

.app {
  min-height: 100vh;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  padding: 2rem 0;
}

input {
  font-size: inherit;
}

.wrapper {
  width: 100%;
  max-width: 992px;
  padding: 0 1rem;
  margin: 0 auto;
}

.title {
  display: inline-block;
  margin: 1.2rem 0;
  font-size: 2rem;
  font-weight: 700;
}

.button {
  padding: 6px 16px;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  transition: 0.4s all ease;
  cursor: pointer;
  min-width: 120px;
  background-color: var(--accent-color);
  color: #fff;
  border: 2px solid var(--accent-color);
}

.button:hover {
  color: var(--accent-color);
  background-color: transparent;
}

.button_red {
  background-color: var(--red-color);
  border-color: var(--red-color);
}

.button_red:hover {
  color: var(--red-color);
  background-color: transparent;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__input,
.button {
  border-radius: 4px;
  height: 2.3rem;
}

.form {
  margin: 1rem 0 2rem;
}

.form__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.field {
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.form__input {
  padding: 6px 18px;
  flex-grow: 1;
}

.form__button {
  flex-shrink: 0;
  margin-left: 1.5rem;
}

.list-item {
  display: flex;
  align-items: center;
  margin-bottom: 0.7rem;
  padding-bottom: 0.7rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  transition: 0.3s all ease;
}

.list-item:last-child {
  margin-bottom: 0;
  border-bottom: none;
}

.list-item__index {
  font-weight: 700;
  font-size: 1.1rem;
  margin-right: 0.5rem;
}

.list-item__text {
  position: relative;
  transition: 0.3s all ease;
}

.list-item__text:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -2%;
  transform: translate(0, -50%);
  height: 2px;
  width: 0;
  background-color: #000;
  transition: 0.3s all ease;
}

.list-item__remove {
  margin-left: auto;
}

.field-checkbox {
  flex-shrink: 0;
  margin-right: 1.2rem;
}

.field-checkbox__box {
  width: 27px;
  height: 27px;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
  display: block;
}

.field-checkbox__box:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  width: 70%;
  height: 70%;
  transition: 0.3s all ease;
  background: url("../images/icon_check.svg") center/contain no-repeat;
}

.field-checkbox__input {
  display: none;
}

.field-checkbox__input:checked + .field-checkbox__box:before {
  opacity: 1;
}

.list-item_completed {
  opacity: 0.6;
}

.list-item_completed .list-item__text:before {
  width: 105%;
}

.alert {
  position: fixed;
  transition: 0.6s opacity ease, 0.6s visibility ease;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 0;
  z-index: 99;
}

.alert__content {
  padding: 2rem 1.6rem;
  background-color: #fff;
  border-radius: 10px;
  width: calc(100% - 30px);
  max-width: 480px;
}

.alert__title {
  font-size: 1.6rem;
  display: block;
  margin-bottom: 1rem;
  font-weight: bold;
}

.alert_opened {
  opacity: 1;
  visibility: visible;
}

.alert .button {
  width: 100%;
}
