a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
main,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
    border: 0;
    margin: 0;
    padding: 0;
    font: inherit;
    vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
    display: block;
}

[hidden] {
    display: none;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:after,
blockquote:before,
q:after,
q:before {
    content: "";
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

a {
    text-decoration: none;
}

button,
textarea,
input {
    background: none;
    border: none;
    appearance: none;
    outline: none;
    resize: none;
    border-radius: 0;
    padding: 0;
}

*:focus {
    outline: none;
}

* {
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

[disabled] {
    cursor: not-allowed;
}

input,
textarea:focus:required:invalid {
    border: none;
}

input,
textarea:required:valid,
input,
textarea:required:invalid {
    border: none;
    box-shadow: none;
}

mark {
    background-color: transparent;
}

::-moz-focus-inner {
    border: 0;
}

* {
    box-sizing: border-box;
}

